<template>
    <div class="map-wrap">
        <a href="https://www.maptiler.com" class="watermark"><img src="https://api.maptiler.com/resources/logo.svg"
                alt="MapTiler logo" style="height: 50px;" /></a>
        <div class="map" ref="mapContainer"></div>
    </div>
</template>
  
<script>
//   import { Map } from 'maplibre-gl';
//   import { shallowRef, onMounted, onUnmounted, markRaw } from 'vue';
import { Map, NavigationControl, Marker, Popup } from 'maplibre-gl';
import { shallowRef, onMounted, onUnmounted, markRaw, toRefs, onUpdated } from 'vue';


export default {
    name: "Map01",
    props: {
        mapscene: {
            type: String,
            default: 'test'
        },
        cntflg: {
            type: Number,
            default: 0
        },
        well: {
            type: Object,
            default: () => { }
        },
        mark: {
            type: Object,
            default: () => { }
        },
    },

    setup(props, contex) {
        const mapContainer = shallowRef(null);
        const map = shallowRef(null);
        const { mapscene, well, cntflg, mark } = toRefs(props)

        onMounted(() => {

            const apiKey = 'yjhNPyQGFJWW44bWBMDi';

            const initialState = { lng: 107.0, lat: -6.15, zoom: 8.55 };

            map.value = markRaw(new Map({
                container: mapContainer.value,
                style: `https://api.maptiler.com/maps/streets-v2/style.json?key=${apiKey}`,
                center: [initialState.lng, initialState.lat],
                zoom: initialState.zoom
            }));

            map.value.addControl(new NavigationControl(), 'top-right');

            console.log(map.value.getZoom())

            well.value.forEach(myInit);
            function myInit(item, index) {
                // console.log(mark.value);
                // console.log(index.place);
                var loc = [];
                loc[0] = item.long
                loc[1] = item.lat

                mark.value[index].popup = new Popup({ closeOnClick: false, offset: 30, className: 'my-class' })
                // .setText(item.text)
                // .setHTML('<div class="flex-container"><div>test 123</div></div>')
                // .setHTML("<h1 class='gunok'>Hello World!</h1><h1 class='gun'>Hello World!</h1>")
                if (item.mode == "card") {
                    mark.value[index].popup.setHTML(`
                    <div class="card card-info card-outline" style="width:160px;">
                        <div class="card-header" style="min-height:5px;height:35px;padding:0">
                            <p style = "font-size:14px;line-height:1.2;text-align:center"><b>Pertamina Zone 1<br>Batang</b></p>
                        </div>
                       
                        <div class="card-body" style="height:60px;padding:0;">
                            
                            <p style="font-size:40px;margin-top:25px;white-space: nowrap;">&nbsp100.5 <span style="font-size:20px">A</span></p>
                            <strong style="position:absolute;top:32px;left:110px"><b>Well-02</b></strong>
                            
                        </div>

                        <div class="card-footer" style="padding:0;text-align:center;">
                            <a href="#">More info</a>
                        </div>
                        
                    </div>
                    `)
                }
                if (item.mode == "icon") {
                    mark.value[index].popup.setHTML(`
                            <div class="info-box" style="min-height:50px;padding:0;">
                            <span class="info-box-icon bg-light" style="width:40px;"><i class="fa fa-stop-circle text-red" style="font-size:25px"></i></span>
                            <div class="info-box-content">
                            <span class="info-box-text text-center"><b>Well-01</b></span>
                            <span class="info-box-text text-center" style="margin-top:-5px">Stop</span>
                            </div>
                            </div>

                        `)
                }
                if (item.mode == "widget") {
                    mark.value[index].popup.setHTML(`
                    <div class="small-box bg-info" style="padding:0">
                        <div class="inner">
                        <h6>150</h6>
                        <p>New Orders</p>
                        </div>
                        <div class="icon">
                            <i class="fas fa-shopping-cart"></i>
                        </div>
                        <a href="#" class="small-box-footer">
                            More info <i class="fas fa-arrow-circle-right"></i>
                        </a>
                    </div>

                        `)
                }
                if (item.mode == "carousel") {
                    mark.value[index].popup.setHTML(`
                    <div id="carouselExampleAutoplaying" class="carousel slide" data-bs-ride="carousel">

                        <div class="carousel-inner">
                            <div class="carousel-item active">

                                <div class="card card-info card-outline" style="width:160px;">
                                    <div class="card-header" style="min-height:5px;height:35px;padding:0">
                                        <p style = "font-size:14px;line-height:1.2;text-align:center"><b>Pertamina Zone 1<br>Batang</b></p>
                                    </div>

                                    <div class="card-body" style="height:60px;padding:0;">
                                        <p style="font-size:40px;margin-top:25px;white-space: nowrap;">&nbsp480 <span style="font-size:20px">Volt</span></p>
                                        <strong style="position:absolute;top:32px;left:110px"><b>Well-02</b></strong>
                                    </div>

                                    <div class="card-footer" style="padding:0;text-align:center;">
                                        <a href="#">More info</a>
                                    </div>
                                </div>

                            </div>

                            <div class="carousel-item">

                                <div class="card card-info card-outline" style="width:160px;">
                                    <div class="card-header" style="min-height:5px;height:35px;padding:0">
                                        <p style = "font-size:14px;line-height:1.2;text-align:center"><b>Pertamina Zone 1<br>Batang</b></p>
                                    </div>
                       
                                    <div class="card-body" style="height:60px;padding:0;">
                                        <div class="info-box" style="min-height:50px;padding:0;width:90%;margin-left:10px;margin-top:5px">
                                        <span class="info-box-icon bg-light" style="width:40px;"><i class="fa fa-play-circle text-green" style="font-size:25px"></i></span>
                                        <div class="info-box-content">
                                        <span class="info-box-text text-center"><b>Well-02</b></span>
                                        <span class="info-box-text text-center" style="margin-top:-5px">Running</span>
                                        </div>
                                        </div>
                                    </div>

                                    <div class="card-footer" style="padding:0;text-align:center;">
                                        <a href="#">More info</a>
                                    </div>
                        
                                </div>

                            </div>

                            <div class="carousel-item">
                                
                                <div class="card card-info card-outline" style="width:160px;">
                                    <div class="card-header" style="min-height:5px;height:35px;padding:0">
                                        <p style = "font-size:14px;line-height:1.2;text-align:center"><b>Pertamina Zone 1<br>Batang</b></p>
                                    </div>

                                    <div class="card-body" style="height:60px;padding:0;">
                                        <p style="font-size:40px;margin-top:25px;white-space: nowrap;">&nbsp100.5 <span style="font-size:20px">A</span></p>
                                        <strong style="position:absolute;top:32px;left:110px"><b>Well-02</b></strong>
                                    </div>

                                    <div class="card-footer" style="padding:0;text-align:center;">
                                        <a href="#">More info</a>
                                    </div>
                                </div>

                            </div>
                            <div class="carousel-item">
                                
                                <div class="card card-info card-outline" style="width:160px;">
                                    <div class="card-header" style="min-height:5px;height:35px;padding:0">
                                        <p style = "font-size:14px;line-height:1.2;text-align:center"><b>Pertamina Zone 1<br>Batang</b></p>
                                    </div>

                                    <div class="card-body" style="height:60px;padding:0;">
                                        <p style="font-size:40px;margin-top:25px;white-space: nowrap;">&nbsp3000 <span style="font-size:20px">Rpm</span></p>
                                        <strong style="position:absolute;top:32px;left:110px"><b>Well-02</b></strong>
                                    </div>

                                    <div class="card-footer" style="padding:0;text-align:center;">
                                        <a href="#">More info</a>
                                    </div>
                                </div>

                            </div>
                        </div>
                        <button class="carousel-control-prev" type="button" data-bs-target="#carouselExampleAutoplaying" data-bs-slide="prev">
                            <span class="carousel-control-prev-icon" aria-hidden="true"></span>
                            <span class="visually-hidden">Previous</span>
                        </button>
                        <button class="carousel-control-next" type="button" data-bs-target="#carouselExampleAutoplaying" data-bs-slide="next">
                            <span class="carousel-control-next-icon" aria-hidden="true"></span>
                            <span class="visually-hidden">Next</span>
                        </button>
                    </div>
                        `)
                }

                if (item.mode == "table") {
                    mark.value[index].popup.setHTML(`
                    <table class="table">
                        <thead style="height:10px">
                            
                                <th scope="col" colspan="3" style="font-size:14px;line-height:1.2;text-align:center;padding:0">
                                    <b>Pertamina Zone 2<br>Rantau</b>
                                </th>
                            
                        </thead>
                        <tbody>
                            <tr>
                            <th scope="row">1</th>
                            <td>Voltage</td>
                            <td>480 V</td>
                            </tr>
                            <tr>
                            <th scope="row">2</th>
                            <td>Ampere</td>
                            <td>100 A</td>
                            </tr>
                            <th scope="row">3</th>
                            <td>Rpm</td>
                            <td>3000</td>
                            </tr>
                            
                        </tbody>
                    </table>
                        `)
                }

                if (item.mode == "cardicon") {
                    mark.value[index].popup.setHTML(`
                    <div class="card card-info card-outline" style="width:160px;">
                        <div class="card-header" style="min-height:5px;height:35px;padding:0">
                            <p style = "font-size:14px;line-height:1.2;text-align:center"><b>Pertamina Zone 2<br>Rantau</b></p>
                        </div>
                       
                        <div class="card-body" style="height:60px;padding:0;">
                            <div class="info-box" style="min-height:50px;padding:0;width:90%;margin-left:10px;margin-top:5px">
                            <span class="info-box-icon bg-light" style="width:40px;"><i class="fa fa-exclamation-triangle text-yellow" style="font-size:25px"></i></span>
                            <div class="info-box-content">
                            <span class="info-box-text text-center"><b>Well-01</b></span>
                            <span class="info-box-text text-center" style="margin-top:-5px">Warning</span>
                            </div>
                            </div>
                        </div>

                        <div class="card-footer" style="padding:0;text-align:center;">
                            <a href="#">More info</a>
                        </div>
                        
                    </div>
                    `)
                }



                if (item.group == "01") {
                    mark.value[index].marker = new Marker({ color: "#FF00FF", draggable: true })
                        .setPopup(mark.value[index].popup)
                        .setLngLat(loc)
                        .addTo(map.value);
                }

                if (item.group == "02") {
                    mark.value[index].marker = new Marker({ color: "#FF0000", draggable: true })
                        .setPopup(mark.value[index].popup)
                        .setLngLat(loc)
                        .addTo(map.value);
                }

                if (item.group == "03") {

                    var label = document.createElement('div');
                    label.id = item.place;
                    label.textContent = item.place;
                    label.style.marginLeft = '-5px';
                    label.style.backgroundColor = '#3498db';
                    // label.style.backgroundColor = '#ff0000';
                    label.style.color = '#ffffff';
                    label.style.height = '12px';
                    label.style.lineHeight = '10px';
                    label.style.padding = '2px';
                    label.style.borderRadius = '4px';
                    label.style.opacity = '1';
                    label.style.fontSize = '8px'; // Set the font size
                    label.style.fontWeight = 'bold'; // Set the font weight
                    label.style.display = 'display'; // Set the font weight

                    mark.value[index].marker = new Marker({ color: "#00FFFF", draggable: true })
                        .setPopup(mark.value[index].popup)
                        .setLngLat(loc)
                        .addTo(map.value);

                    mark.value[index].marker.getElement().appendChild(label);







                }

                // setInterval(function () {
                //     var blink = document.getElementById('banjarmasin');
                //     blink.style.opacity = (blink.style.opacity == 0 ? 1 : 0);
                //     console.log(blink.style.opacity);
                // }, 2000);

                map.value.on('click', function (e) {
                    // e.lngLat is the clicked location in longitude and latitude
                    console.log('Clicked at:', e.lngLat);
                });

                map.value.on('zoom', function (e) {
                    // e.target.getZoom() provides the current zoom level
                    console.log('Current Zoom Level:', e.target.getZoom());
                });





            }
        }),

            onUnmounted(() => {
                map.value?.remove();
            }),

            onUpdated(() => {

            })

        return {
            map, mapContainer
        };
    },


};
</script>
  
  
<style scoped>
@import '~maplibre-gl/dist/maplibre-gl.css';

.map-wrap {
    position: relative;
    width: 100%;
    height: 100%;
    /* calculate height of the screen minus the heading */
}

.map {
    position: absolute;
    width: 100%;
    height: 100%;
}

.watermark {
    position: relative;
    left: 10px;
    bottom: 10px;
    z-index: 999;
    display: none;
}
</style>

<style>
.map-wrap .gun {
    color: blue
}

