/* background: transparent; */
/* width: 10%; */
/* left: 0px;
    top: 0px; */
/* padding: 5px; */
/* height: 1px; */
/* } */

.maplibregl-popup-content {
    /* width: 100px; */
    padding: 0px;
    /* margin-bottom: 100px; */
    /* height: 55px; */
    border-radius: 10px;
    background: none;
    box-shadow: none;
}

.maplibregl-popup-tip {
    margin-top: -20px;

}

.maplibregl-popup-close-button {
    display: none;
}
</style>